
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'


export default {
components: {
Group,
Cell,
XButton,
Divider
},
data () {
return {




msg: 'Hello World!',
question:{text: "沙口测试", result: 0},
testResult:{text: ""},
questions:[
{text: '水雷', result: 1},
{text: '火雷', result: 2},
{text: '风雷', result: 3},
{text: '火水', result: 4},
{text: '风水', result: 5}
],
answers:[
{text: '外前后', result: 1},
{text: '外左右', result: 2},
{text: '外X', result: 3},
{text: '内左右', result: 4},
{text: '内X', result: 5}
],
answerSet:[]
}
},
methods: {
setQuestion() {
const index = Math.floor(Math.random() * this.questions.length);
this.question = this.questions[index];
this.answerSet = this.setRandomAnswer(this.answers);
this.testResult = {text: ""};
},
setRandomAnswer(array) {
let currentIndex = array.length, temporaryValue, randomIndex;


while (0 !== currentIndex) {


randomIndex = Math.floor(Math.random() * currentIndex);
currentIndex -= 1;


temporaryValue = array[currentIndex];
array[currentIndex] = array[randomIndex];
array[randomIndex] = temporaryValue;
}
return array;
},
testResultWithAnswer(answer) {
console.log(this.question);
console.log(answer);
if (this.question.result == answer.result) {
this.testResult.text = "还行";
console.log("correct");
} else {
console.log("沙口");
this.testResult.text = "沙口";
}

}
}
}




